import React, { Component } from 'react';
import { Link, navigate } from "gatsby"
import wordmark from '../../public/rivian_wordmark.png'

export default class extends Component {
  constructor(props) {
    super(props);

    const navigation = [
      {
        name: "Rivian",
        path: "/"
      },
      {
        name: "R1T",
        path: "/r1t/"
      },
      {
        name: "R1S",
        path: "/r1s/"
      },
      {
        name: "Technology",
        path: "/technology/"
      },
      {
        name: "About",
        path: "/about/"
      }
    ]

    this.state = {
      windowHeight: 0,
      sticky: false,
      navigation
    }

    // this.handleScroll = this.handleScroll.bind(this);
    this.navigateNext = this.navigateNext.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
  }

  componentDidMount() {
    // document.addEventListener('scroll', this.handleScroll, {
    //   capture: true,
    //   passive: true
    // });

    document.addEventListener('keydown', this.handleKeyDown)

    // this.setState({
    //   windowHeight: window.innerHeight
    // });
  }

  // componentWillUnmount() {
  //   document.removeEventListener('scroll', this.handleScroll);
  // }

  // handleScroll() {
  //   if (window.pageYOffset > (window.innerHeight - 115) && !this.state.sticky) {
  //     this.setState({
  //       sticky: true
  //     });
  //   } else if (window.pageYOffset <= (window.innerHeight - 115) && this.state.sticky) {
  //     this.setState({
  //       sticky: false
  //     });
  //   }
  // }

  navigateNext(direction) {
    let children = this.links.children;

    for (const [index, child] of Object.entries(children)) {

      if (child.getElementsByTagName('a')[0] && child.getElementsByTagName('a')[0].classList.contains('active')) {

        if (direction === "left") {
          if (index > 0) {
            navigate(this.state.navigation[index - 1].path + "/");
          }
        } else if (direction === "right") {
          if (index < this.state.navigation.length - 1) {
            navigate(this.state.navigation[parseInt(index) + 1].path + "/");
          }
        }

        this.setState({
          activeItemWidth: child.getElementsByTagName('a')[0].offsetWidth,
          activeItemOffset: child.offsetLeft
        });
      }
    }
  }

  handleClick(el) {
    this.navigateNext(el.currentTarget.dataset.direction);
  }

  handleKeyDown(key) {
    if (key.code) {
      if (key.code === "ArrowRight") {
        this.navigateNext("right");
      } else if (key.code === "ArrowLeft") {
        this.navigateNext("left");
      }
    }
  }

  render() {
    let highlightStyle = {
      width: this.state.activeItemWidth,
      marginLeft: this.state.activeItemOffset
    }



    return (
      <React.Fragment>
        <nav>
          <Link className="rm_nav-home" to="/"><img alt="wordmark" src={wordmark} /></Link>
          <ul ref={links => this.links = links} className={`rm_nav-dock`}>
            {this.state.navigation.map(item => <li key={item.name} className="rm_nav-link"><Link activeClassName="active" to={item.path}>{item.name}</Link></li>)}
            <hr style={highlightStyle} />
          </ul>
        </nav>
       {/* <div className="rm_nav-controls">
          <button className="button arrow-left" data-direction="left" onClick={this.handleClick}></button>
          <button className="button arrow-right" data-direction="right" onClick={this.handleClick}></button>
        </div>*/}

      </React.Fragment>
    )
  }
}
