import React, { Component } from 'react';
import { Link, StaticQuery, graphql } from "gatsby";
import Carousel from './Carousel';
import Img from "gatsby-image";

export default class Hero extends Component {
  constructor(props) {
    super(props);

    this.state = {
      focusOffset: 0,
      width: 1200,
      offset: 0
    }

    this.renderHeros = this.renderHeros.bind(this);
    this.renderbg = this.renderbg.bind(this);
  }
  componentDidMount() {
    this.setState({
      width: window.innerWidth
    })
  }

  renderHeros() {
    if (this.props.location) {
      return this.props.routes.map((route,i) => {
        let style = {
          minWidth: this.state.width,
          transform: `translateX(${this.props.location.pathname === route.path ? 0 : -300}px)`
        }
        return (
          <div className="rm_hero-item" key={i}>
            <img className={`rm_hero-bg ${this.props.location.pathname === route.path ? "active" : ""}`} src={require(`../images/${route.bg}`)} />
            <div style={style}  className={`rm_hero_cta ${this.props.location.pathname === route.path ? "active" : ""}`}>
              <span className="rm-v-guide"></span>
              <h6 className="rm_hero_subtitle">{route.subtitle}</h6>
              <h1 className="rm_hero_title">{route.title}</h1>
            </div>
          </div>
        )
      })
    }

  }

  renderbg() {
    for (const route of this.props.routes) {

      if (this.props.location.pathname === route.path) {

        return <img key={Math.random()} className={`rm_hero-bg`} src={require(`../images/${route.bg}`)} />
      }
    }
  }



  render() {
    let active = 0;

    this.props.routes.forEach((route, i) => {

      if (this.props.location && this.props.location.pathname === route.path) {
        active = i;
      }

    });

    let image = "hero.jpg"

    return (
      <div className="rm_hero">



        <Carousel offset={this.state.offset} width={this.state.width} activeIndex={active}>
          {this.renderHeros()}
        </Carousel>

        <div className="rm_guide"></div>
        <button className="button button-cta">Preorder now</button>
      </div>
    )
  }
}

Hero.defaultProps = {
  routes: [
    {
      name: "Rivian",
      path: "/",
      title: "Electric \n\n Adventure \n\n Vehicle",
      subtitle: "The world's first",
      bg: "hero.jpg"
    },
    {
      name: "R1T",
      path: "/r1t/",
      title: "R1T",
      subtitle: "2020",
      bg: "hero_r1t.jpg"
    },
    {
      name: "R1S",
      path: "/r1s/",
      title: "R1S",
      subtitle: "2021",
      bg: "hero_r1s.jpg"
    },
    {
      name: "Technology",
      path: "/technology/",
      title: "Technology",
      subtitle: "The world's first",
      bg: "hero.jpg"
    },
    {
      name: "About",
      path: "/about/",
      title: "About",
      subtitle: "The world's first",
      bg: "hero.jpg"
    }
  ]
}
